/* Css for header */

.header {
  padding: 0 1.063rem 0 1.063rem;
  width: 90%;
  display: flex;
  justify-content: space-between;
  position: fixed;
  top: 0;
  z-index: 1;
}

.header .logo p {
  font-family: "Dancing Script";
  font-style: normal;
  font-weight: 700;
  font-size: 45px;
  color: #ffffff;
  margin: 0;
}

.header .header-items {
  /* display: flex; */
  display: none;
  justify-content: space-between;
  align-items: center;
  font-family: "Exo 2";
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  color: #FFFFFF;
  width: 60%;
}

.header .call {
  width: 131px;
  height: 50px;
  border: 2px solid #ffffff;
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.menu{
display: flex;
align-items: center;
}

.menu .icon{
  width: 40px;
height: 45.71px;
color: white;
}

.header .menu-items{
  width: 170px;
  position: absolute;
  top: 50px;
  right: 0;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  z-index: 1;
  background: linear-gradient(
    94.43deg,
    rgba(249, 249, 249, 0.49) 34.95%,
    rgba(249, 249, 249, 0.175) 67.01%
  );
  backdrop-filter: blur(5px);
  padding: 0.8rem;
  color: rgb(99, 95, 95);
  font-weight: 700;
  font-size: 20px;
  border-radius: 10px;
  font-family: "Exo 2";
  transition: height 5s;
}

@media screen and (min-width: 768px) {
  .header {
    padding: 0 4.063rem 0 4.063rem;
  }

  .header .header-items {
    display: flex;
  }

  .header .menu{
    display: none;
  }
  

  .header .menu-items{
    display: none;
  }
}
